<template>
  <div class="chartSetting">
     <b-code-editor v-model="result" :indent-unit="4" height="auto" @on-change='onJsonChange'/>


     <!-- 中文支持差 -->
    <!-- <JsonEditor v-model="result" lang="zh" mode='code' :show-btns="false" @json-change="onJsonChange"></JsonEditor> -->
  </div>
</template>

<script>
// import JsonEditor from 'vue-json-editor';

export default {
  name: 'chartSetting',
  props: {
    value: {
      require: true,
    },
  },
  data() {
    return {
      result: {},
      temp:''
    };
  },
  // components: { JsonEditor },
  methods: {
    onJsonChange(value) {
      this.result = value;
      // this.$emit('update', JSON.parse(value));
    },
    jsonUpdate() {
      if(this.temp===this.result){
        return
      }
      return  JSON.parse(this.result);
      this.$emit('update', this.result);
    },
  },

  watch: {
    value: {
      deep: true,
      immediate: true,
      handler(v) {
        this.result = JSON.stringify(v,null,4);
        this.temp = this.result;
      },
    },
  },
};
</script>

<style lang='scss' >
.chartSetting {
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  overflow-y: scroll;
}
.jsoneditor-vue {
  position: absolute;
  height: 100%;
  width: 100%;
}
.bin-json-editor ,.CodeMirror {
  position: absolute;
  height: 100% !important;
  width: 100%;
}
.jsoneditor-modes {
  display: none;
}
</style>